@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,600;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.bodyContent{
    font-family: 'Cormorant Garamond', serif;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* background: radial-gradient(#A5FECB,#20BDFF,#5433FF); */
    /* background: radial-gradient(#ffdde1,#ee9ca7); */
    /* background: linear-gradient(to right,#C6FFDD,#FBD786,#f7797d); */
    background-image: url(/music-player-js/static/media/bg.600123cb.jpg);
    /* background: #514A9D; */
    /* background: #07102e; */
}

.input-area{
    position: relative;
    bottom: 50px;
    display: flex;
}

.icon{
    width: 20px;
    background: rgba(255, 255, 255, 0.187);
    border: none;
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 4px );
    padding-right: 3px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;

}

.icon:hover{
    cursor: pointer;
}

input{
    width: 300px;
    height: 50px;
    padding: 2px 5px 2px 9px ;
    background: rgba(255, 255, 255, 0.187);
    border: none;
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 4px );
    border-radius: 10px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;

    font-size: 20px;
    font-family: 'Cormorant Garamond', serif;
    color: black;
}

.PlayerCard{
    width: 400px;
    height: 420px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background: rgba(255, 255, 255, 0.187);
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 4px );
    border-radius: 10px;
}

.PlayerCard h1{
    font-size: 16px;
}


.songImg{
    width: 50%;
    height: 50%;
    border-radius: 50%;
}


.motionIcons{
    height: 10%;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: relative;
    bottom: 14px;
}


@-webkit-keyframes spin { 100% { transform:rotate(360deg); } }


@keyframes spin { 100% { transform:rotate(360deg); } }
